/* @import 'fonts'; */
* {
  box-sizing: border-box;
}

@font-face {
  font-family: 'Digital Numbers Regular';
  font-style: normal;
  font-weight: normal;
  src: local('Digital Numbers Regular'),
    url('../fonts/digital-numbers-cufonfonts-webfont/DigitalNumbers-Regular.woff')
      format('woff');
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  position: relative;
  display: flex;
  justify-content: center;
}

img {
  display: block;
  max-width: 100%;
}
p {
  margin: 0;
  padding: 0;
}
.house {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  width: 440px;
  /* border: 10px solid rgb(84, 53, 34); */
  outline: 10px solid rgb(84, 53, 34);
}
.floor {
  position: relative;
  display: flex;
  align-items: center;
  height: 200px;
  background-color: rgb(255, 251, 192);
  outline: 5px solid rgb(84, 53, 34);
}
.top-block {
  position: absolute;
  top: -6px;
  height: 48px;
  width: 440px;
  z-index: 2;
  background-color: rgb(255, 251, 192);
  border-top: 12px solid rgb(84, 53, 34);
}

.bottom-block {
  position: absolute;
  top: 194px;
  height: 7px;
  width: 440px;
  z-index: 3;
  background-color: rgb(255, 251, 192);
  border-bottom: 5px solid rgb(84, 53, 34);
}

.lift {
  position: absolute;
  z-index: 1;
  background-color: rgb(198, 198, 198);
  border-width: 20px;
  border-style: inset;
  border-color: buttonborder;
  width: 100px;
  height: 147px;
  top: 844px;
  left: 12px;
}

.outdoor {
  position: relative;
  z-index: 2;
  border-width: 2px;
  border-style: double;
  width: 104px;
  height: 151px;
  top: 18px;
  left: 10px;
  background-color: #00000079;
  transform: translateX(0);
  transition: 2000ms cubic-bezier(0.4, 0, 0.2, 1);
}

.outdoor-open {
  transform: translateX(-100%);
}

.lift-button {
  border-radius: 50%;
  padding: 3px 6px;
  border-width: 1px;
  font-size: 10px;
  cursor: pointer;
  background-color: #efefef;
  color: #000000;
}

.button__call {
  position: absolute;
  top: 100px;
  left: 120px;
  border-radius: 50%;
  background-color: #efefef;
  padding: 7px 7px;
  border-width: 2px;
  cursor: pointer;
}
/* =========== */

.tab {
  z-index: 3;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 9px;
  left: 46px;
  width: 30px;
  height: 30px;
  background-color: rgb(0, 0, 0);
  border: 2px solid #afb1b8;
  border-radius: 50%;
}
.tab__quad {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30%;
  width: 15px;
  height: 20px;
  background-color: rgb(68, 0, 255);
  border: 2px solid #afb1b8;
}

.tab__quad-num {
  display: block;
  font-family: 'Digital Numbers Regular';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 0.7;
  letter-spacing: 0.01px;
  color: #ffffff;
  text-shadow: 0px 0px 1px rgb(255, 255, 255),
    0px 0px 3px rgba(255, 255, 255, 0.468);
}
